import React from "react";
import { useSelector, useDispatch } from "react-redux";
import { useTranslation } from "react-i18next";
import DesignSystemButton from "../DesignSystemButton/DesignSystemButton";
import SaveIcon from "../../assets/icons/save.svg";
import { triggerSave } from "../../redux/EditorSlice";
import {
  showSavedMessage,
} from "../../utils/Notifications";

const SaveButton = ({ className, type = "secondary" }) => {
  const dispatch = useDispatch();
  const { t } = useTranslation();

  const loading = useSelector((state) => state.editor.loading);

  const project = useSelector((state) => state.editor.project);
  const onClickSave = async () => {
    if (window.plausible) {
      window.plausible("Save button");
    }
    dispatch(triggerSave());
    localStorage.setItem("project", JSON.stringify(project));
    //Sende das project ohne beachtung der cors regeln an /ide4school/ide4&clicksave mit einem XMLhttprequest
    const xhr = new XMLHttpRequest();
    xhr.open("POST", "/ide4school/ide&clicksave", true);
    xhr.setRequestHeader("Content-Type", "application/json");
    xhr.send(JSON.stringify(project));
    xhr.onreadystatechange = function () {
      //Wenn die Antwort vom Server kommt und der Status 200 ist, dann zeige eine Erfolgsmeldung an, ersetzte den identifier in project durch den zurückgegebenen identifier und speichere das Projekt in den LocalStorage
      if (xhr.readyState === 4 && xhr.status === 200) {
        showSavedMessage();
        if(JSON.parse(xhr.responseText).saveNew === true){
          const ident = JSON.parse(xhr.responseText).identifier;
          window.location.href = "/ide4school/clicksaverefresh&ident="+ident;
        }
      }
      else if (xhr.readyState === 4 && xhr.status !== 200) {
        alert("Fehler beim Speichern. Bitte versuche es erneut.");
      }
    };

  };

  return (
    loading === "success" && (
      <DesignSystemButton
        className={className}
        //Wenn das to_review = true dann ist keine OnClick Funktion hinterlegt, ansonsten wird die Funktion onClickSave ausgeführt
        onClick={project.to_review ? null : onClickSave}
        text={project.to_review ? t("header.readonly") : t("header.save")}
        textAlways
        icon={<SaveIcon />}
        type={type}
        disabled={project.to_review}
      />
    )
  );
};

export default SaveButton;
