import { useEffect } from "react";
import { useDispatch } from "react-redux";
import { isOwner } from "../utils/projectHelpers";
import { expireJustLoaded, syncProject } from "../redux/EditorSlice";

export const useProjectPersistence = ({
  user,
  project,
  justLoaded,
  hasShownSavePrompt,
  saveTriggered,
}) => {
  const dispatch = useDispatch();

  useEffect(() => {
    if (saveTriggered) {
      if (isOwner(user, project)) {
        dispatch(
          syncProject("save")({
            project,
            accessToken: user.access_token,
            autosave: false,
          }),
        );
      } else if (user && project.identifier) {
        dispatch(
          syncProject("remix")({ project, accessToken: user.access_token }),
        );
      }
    }
  }, [saveTriggered, project, user, dispatch]);

  useEffect(() => {
    if (user && localStorage.getItem("awaitingSave")) {
      if (isOwner(user, project)) {
        dispatch(
          syncProject("save")({
            project,
            accessToken: user.access_token,
            autosave: false,
          }),
        );
      } else if (user && project.identifier) {
        dispatch(
          syncProject("remix")({ project, accessToken: user.access_token }),
        );
      }
      localStorage.removeItem("awaitingSave");
      return;
    }
    let debouncer = setTimeout(() => {
      if (isOwner(user, project) && project.identifier) {
        if (justLoaded) {
          dispatch(expireJustLoaded());
        }
        dispatch(
          syncProject("save")({
            project,
            accessToken: user.access_token,
            autosave: true,
          }),
        );
      } else {
        if (justLoaded) {
          dispatch(expireJustLoaded());
        } else {
          if (project.to_review == false) {
            localStorage.setItem("project", JSON.stringify(project));
            const xhr = new XMLHttpRequest();
            xhr.open("POST", "/ide4school/ide&autosave", true);
            xhr.setRequestHeader("Content-Type", "application/json");
            xhr.send(JSON.stringify(project));
            xhr.onreadystatechange = function () {
              if (xhr.readyState === 4 && xhr.status === 200) {
                console.log("Autosave complete.");
              }
              else if (xhr.readyState === 4 && xhr.status !== 200) {
                console.log("Autosave failed.");
              }
          };
          }
        }
      }
    }, 2000);

    return () => clearTimeout(debouncer);
  }, [dispatch, project, user, hasShownSavePrompt, justLoaded]);
};
